:root {
  --primary-bg: #1e1e1e;
  --secondary-bg: #1e1e1e;
  --accent-color: #ffd700;
  --text-color: #ffffff;
  --success-color: #4CAF50;
  --error-color: #ff4444;
}

body, html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Arial', sans-serif;
  background-color: var(--primary-bg);
  color: var(--text-color);
  width: 100%;
  height: 100%;
  overflow-y: auto;
}

body {
  padding-top: 70px; /* Account for fixed navbar */
}

#root, .App {
  min-height: 100vh;
  background-color: var(--primary-bg);
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  background-color: transparent;
}

.game-section {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  margin-bottom: 2rem;
  background-color: transparent;
}

/* Typography */
h1, h2, h3, h4, h5, h6 {
  color: var(--accent-color);
  margin-bottom: 1rem;
}

/* Buttons */
.btn {
  background-color: var(--accent-color);
  color: var(--primary-bg);
  border: none;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.btn:hover {
  opacity: 0.9;
  transform: translateY(-2px);
}

/* Cards */
.card {
  background-color: var(--secondary-bg);
  border-radius: 10px;
  padding: 1.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 1rem;
}

/* Utility classes */
.text-center {
  text-align: center;
}

.mb-1 {
  margin-bottom: 0.5rem;
}

.mb-2 {
  margin-bottom: 1rem;
}

.mt-1 {
  margin-top: 0.5rem;
}

.mt-2 {
  margin-top: 1rem;
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.fade-in {
  animation: fadeIn 0.5s ease-in-out;
}

/* Responsive design */
@media (max-width: 768px) {
  .game-section {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  body {
    font-size: 14px;
  }

  .main-container {
    padding: 1rem;
  }
}

/* Ensure black background */
body::before,
body::after,
#root::before,
#root::after,
.App::before,
.App::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--primary-bg);
  z-index: -1;
}

