.house-wallet-display {
  background-color: var(--secondary-bg);
  border: 4px solid var(--accent-color);
  border-radius: 15px;
  padding: 1.5rem;
  width: 100%;
  max-width: 300px;
  height: 100%;
  max-height: 300px;
  text-align: center;
  box-shadow: 0 0 20px rgba(255, 215, 0, 0.2);
  margin: 1rem auto;
  transition: all 0.3s ease;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.house-wallet-display:hover {
  transform: scale(1.02);
  box-shadow: 0 0 25px rgba(255, 215, 0, 0.3);
}

.house-wallet-display h2 {
  font-size: 1.8rem;
  color: var(--accent-color);
  margin-bottom: 1rem;
  text-transform: uppercase;
  text-shadow: 0 0 5px rgba(255, 215, 0, 0.5);
}

.vault-door {
  width: 120px;
  height: 120px;
  background-color: var(--primary-bg);
  border: 6px solid var(--accent-color);
  border-radius: 50%;
  margin: 0 auto 1rem;
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease;
}

.vault-door:hover {
  transform: rotate(15deg);
}

.vault-dial {
  width: 60px;
  height: 60px;
  background-color: var(--secondary-bg);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  transition: transform 0.3s ease;
}

.vault-door:hover .vault-dial {
  transform: translate(-50%, -50%) rotate(90deg);
}

.vault-handle {
  width: 30px;
  height: 8px;
  background-color: var(--accent-color);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  transition: transform 0.3s ease;
}

.vault-door:hover .vault-handle {
  transform: translate(-50%, -50%) rotate(135deg);
}

.house-balance {
  font-size: 1.8rem;
  font-weight: bold;
  color: var(--success-color);
  text-shadow: 0 0 10px rgba(0, 255, 0, 0.3);
  margin-top: 1rem;
  transition: color 0.3s ease;
}

.house-balance:hover {
  color: var(--accent-color);
}

@media (max-width: 768px) {
  .house-wallet-display {
    padding: 1.2rem;
  }

  .house-wallet-display h2 {
    font-size: 1.6rem;
  }

  .vault-door {
    width: 100px;
    height: 100px;
  }

  .vault-dial {
    width: 50px;
    height: 50px;
  }

  .vault-handle {
    width: 25px;
    height: 6px;
  }

  .house-balance {
    font-size: 1.6rem;
  }
}

@media (max-width: 480px) {
  .house-wallet-display {
    padding: 1rem;
  }

  .house-wallet-display h2 {
    font-size: 1.4rem;
  }

  .vault-door {
    width: 80px;
    height: 80px;
  }

  .vault-dial {
    width: 40px;
    height: 40px;
  }

  .vault-handle {
    width: 20px;
    height: 5px;
  }

  .house-balance {
    font-size: 1.4rem;
  }
}