.betting-panel {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #1e1e1e !important;
  border: 2px solid var(--accent-color);
  border-radius: 10px;
  padding: 1.5rem;
  width: 100%;
  max-width: 400px;
  height: 100%;
  max-height: 600px;
  text-align: center;
  margin: 2rem auto;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.bet-type-buttons {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 1rem;
}

.bet-type-buttons button {
  background-color: var(--primary-bg);
  color: var(--accent-color);
  border: 1px solid var(--accent-color);
  padding: 0.5rem;
  cursor: pointer;
  border-radius: 5px;
  flex: 1;
  margin: 0 0.25rem;
  transition: all 0.3s ease;
}

.bet-type-buttons button.active {
  background-color: var(--accent-color);
  color: var(--primary-bg);
}

.bet-type-buttons button:hover {
  background-color: var(--accent-color);
  color: var(--primary-bg);
}

.input-group {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 1rem;
  width: 100%;
}

.input-group label {
  margin-bottom: 0.5rem;
  font-weight: bold;
  color: var(--text-color);
}

.input-group input {
  width: 100%;
  padding: 0.5rem;
  background-color: var(--primary-bg);
  border: 2px solid var(--accent-color);
  border-radius: 5px;
  text-align: center;
  font-size: 1rem;
  color: var(--text-color);
}

.bet-buttons {
  display: flex;
  justify-content: space-around;
  width: 100%;
  margin-bottom: 1rem;
}

.bet-buttons button {
  background-color: var(--primary-bg);
  color: var(--accent-color);
  border: 1px solid var(--accent-color);
  padding: 0.5rem 1rem;
  cursor: pointer;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.bet-buttons button:hover {
  background-color: var(--accent-color);
  color: var(--primary-bg);
}

.roll-button {
  width: 100%;
  background-color: var(--accent-color);
  color: var(--primary-bg);
  border: none;
  padding: 0.75rem;
  cursor: pointer;
  border-radius: 5px;
  font-size: 1.1rem;
  font-weight: bold;
  transition: all 0.3s ease;
}

.roll-button:hover {
  opacity: 0.9;
  transform: translateY(-2px);
}

.roll-button:disabled {
  background-color: #2a2a2a;
  color: var(--text-color);
  cursor: not-allowed;
  opacity: 0.7;
}

.result-message {
  margin-top: 1.5rem;
  padding: 1rem;
  border-radius: 10px;
  text-align: center;
  font-weight: bold;
  width: 100%;
}

.result-message.win {
  background-color: rgba(0, 255, 0, 0.1);
  border: 2px solid var(--success-color);
  color: var(--success-color);
}

.result-message.loss {
  background-color: rgba(255, 0, 0, 0.1);
  border: 2px solid var(--error-color);
  color: var(--error-color);
}

.result-message h3 {
  margin-top: 0;
  font-size: 1.5rem;
}

.result-message p {
  margin: 0.5rem 0;
}

.auto-roll-buttons {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 1rem;
}

.auto-roll-buttons button {
  background-color: var(--primary-bg);
  color: var(--accent-color);
  border: 1px solid var(--accent-color);
  padding: 0.5rem 1rem;
  cursor: pointer;
  border-radius: 5px;
  flex: 1;
  margin: 0 0.25rem;
  transition: all 0.3s ease;
}

.auto-roll-buttons button:hover {
  background-color: var(--accent-color);
  color: var(--primary-bg);
}

.auto-roll-buttons button:disabled {
  background-color: #2a2a2a;
  color: var(--text-color);
  cursor: not-allowed;
  opacity: 0.7;
}

@media (max-width: 768px) {
  .betting-panel {
    padding: 1rem;
  }

  .bet-type-buttons button,
  .bet-buttons button,
  .auto-roll-buttons button {
    padding: 0.4rem 0.8rem;
    font-size: 0.9rem;
  }

  .input-group input {
    font-size: 0.9rem;
  }

  .roll-button {
    padding: 0.6rem;
    font-size: 1rem;
  }

  .result-message h3 {
    font-size: 1.3rem;
  }
}

@media (max-width: 480px) {
  .betting-panel {
    padding: 0.8rem;
  }

  .bet-type-buttons,
  .bet-buttons,
  .auto-roll-buttons {
    flex-wrap: wrap;
  }

  .bet-type-buttons button,
  .bet-buttons button,
  .auto-roll-buttons button {
    flex: 0 0 calc(50% - 0.5rem);
    margin: 0.25rem;
  }

  .input-group input {
    font-size: 0.8rem;
  }

  .roll-button {
    padding: 0.5rem;
    font-size: 0.9rem;
  }

  .result-message h3 {
    font-size: 1.2rem;
  }
}