body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  background-color: #1e1e1e;
  color: #ffffff;
  font-family: Arial, sans-serif;
}

#root {
  min-height: 100%;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  position: relative;
}

.main-content {
  flex: 1;
  padding: 20px;
  padding-top: 70px; /* Adjust this value based on your navbar height */
  overflow-y: auto;
  position: relative;
  z-index: 1;
}

.game-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto;
}


@media (min-width: 768px) {
  .game-container {
    flex-direction: row;
  }

  .game-panel {
    flex: 2;
  }

  .leaderboard {
    flex: 1;
  }
}

/* Ensure Starfield doesn't interfere with scrolling */
canvas#starfield {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  pointer-events: none;
}

/* Navbar styles */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  background-color: rgba(30, 30, 30, 0.8);
  backdrop-filter: blur(5px);
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .main-content {
    padding: 10px;
    padding-top: 60px; /* Adjust for smaller navbar on mobile */
  }

  .game-container {
    flex-direction: column;
  }

  .game-panel, .leaderboard {
    width: 100%;
  }
}

/* Additional styles to ensure content is above Starfield */
.leaderboard, .betting-panel, .dice-roller-container {
  position: relative;
  z-index: 2;
}