.dice-roller-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 2rem;
  width: 100%;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
  backdrop-filter: blur(10px);
  background-color: #0f0f0f;
  border-radius: 15px;
  padding: 2rem;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.1);
}

.dice-display {
  position: relative;
  width: 200px;
  height: 200px;
  margin-bottom: 2rem;
  transition: transform 0.3s ease;
  border-radius: 15px;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.2);
  border: 2px solid #4CAF50;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.288);
}

.dice-display:hover {
  transform: scale(1.05);
}

.dice-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
  border-radius: 15px;
  transition: transform 0.5s ease;
}

.dice-image.spinning {
  animation: spin 1s linear infinite;
  
}

@keyframes spin {
  0% {
    transform: rotate(0deg) scale(1);
  }
  100% {
    transform: rotate(360deg) scale(1);
    
  }
}

.dice-number {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  font-weight: bold;
  color: #4CAF50;
  text-shadow: 0 0 10px rgba(76, 175, 80, 0.7);
}

.roll-container {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.5rem;
  border-radius: 10px;
  margin-bottom: 1rem;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.1);
  border: 1px solid #4CAF50;
  backdrop-filter: blur(5px);
  background-color: rgba(0, 0, 0, 0.6);
}

.payout-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  width: 100%;
  border-radius: 10px;
  margin-top: 1rem;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.1);
  border: 1px solid #4CAF50;
  backdrop-filter: blur(5px);
  background-color: rgba(0, 0, 0, 0.6);
}

.payout-text {
  font-size: 1.2rem;
  font-weight: bold;
  color: #4CAF50;
  text-shadow: 0 0 10px rgba(76, 175, 80, 0.7);
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

.roll-button {
  background-color: #4CAF50;
  color: #ffffff;
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1.2rem;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
  box-shadow: 0 0 10px rgba(76, 175, 80, 0.5);
}

.roll-button:hover {
  background-color: #45a049;
  box-shadow: 0 0 15px rgba(76, 175, 80, 0.7);
  transform: translateY(-2px);
}

.roll-button:active {
  transform: translateY(0);
}

/* Modal styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: rgba(0, 0, 0, 0.9);
  padding: 2rem;
  border-radius: 10px;
  max-width: 80%;
  max-height: 80%;
  overflow-y: auto;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
  border: 2px solid #4CAF50;
  color: #ffffff;
  backdrop-filter: blur(10px);
}

.modal-content h2 {
  color: #4CAF50;
  margin-bottom: 1rem;
}

.modal-content p {
  margin-bottom: 1rem;
  line-height: 1.5;
}

.modal-content button {
  background-color: #4CAF50;
  color: #ffffff;
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 0 10px rgba(76, 175, 80, 0.5);
}

.modal-content button:hover {
  background-color: #45a049;
  box-shadow: 0 0 15px rgba(76, 175, 80, 0.7);
  transform: translateY(-2px);
}

/* Info button styles */
.info-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background-color: #4CAF50;
  color: #ffffff;
  border: none;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 0 10px rgba(76, 175, 80, 0.5);
}

.info-button:hover {
  background-color: #45a049;
  box-shadow: 0 0 15px rgba(76, 175, 80, 0.7);
  transform: translateY(-2px);
}

/* Specific modal content styles */
.disclaimer-modal-content {
  text-align: center;
}

.info-modal-content h3 {
  color: #4CAF50;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
}

.info-modal-content ul {
  margin-bottom: 1rem;
  padding-left: 1.5rem;
}

/* Betting panel styles */
.betting-panel {
  width: 100%;
  border-radius: 10px;
  padding: 1.5rem;
  margin-top: 1rem;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.1);
  border: 1px solid #4CAF50;
  backdrop-filter: blur(5px);
  background-color: rgba(0, 0, 0, 0.6);
}

.bet-type-buttons {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.bet-type-buttons button {
  background-color: rgba(76, 175, 80, 0.3);
  color: #ffffff;
  border: none;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.bet-type-buttons button.active {
  background-color: #4CAF50;
  box-shadow: 0 0 10px rgba(76, 175, 80, 0.5);
}

.input-group {
  margin-bottom: 1rem;
}

.input-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: #ffffff;
}

.input-group input {
  width: 100%;
  padding: 0.5rem;
  font-size: 1rem;
  border-radius: 5px;
  border: 1px solid #4CAF50;
  background-color: rgba(255, 255, 255, 0.1);
  color: #ffffff;
}

.bet-buttons {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.bet-buttons button {
  background-color: rgba(76, 175, 80, 0.3);
  color: #ffffff;
  border: none;
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  font-weight: bold;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.bet-buttons button:hover {
  background-color: rgba(76, 175, 80, 0.5);
}

.transaction-status {
  margin-top: 1rem;
  padding: 1rem;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  backdrop-filter: blur(5px);
}

.transaction-status.error {
  background-color: rgba(255, 0, 0, 0.2);
  border: 1px solid #ff0000;
}

.result-message {
  margin-top: 1rem;
  padding: 1rem;
  border-radius: 5px;
  text-align: center;
  backdrop-filter: blur(5px);
}

.result-message.win {
  background-color: rgba(76, 175, 80, 0.2);
  border: 1px solid #4CAF50;
  color: #4CAF50;
}

.result-message.loss {
  background-color: rgba(255, 0, 0, 0.2);
  border: 1px solid #ff0000;
  color: #ff0000;
}

/* House performance styles */
.house-performance {
  width: 100%;
  margin-top: 2rem;
  padding: 1.5rem;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.6);
  border: 1px solid #4CAF50;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(5px);
}

.house-performance h3 {
  color: #4CAF50;
  margin-bottom: 1rem;
  text-align: center;
}

.house-performance p {
  margin-bottom: 0.5rem;
  color: #ffffff;
}

@media (max-width: 768px) {
  .dice-roller-container {
    padding: 1.5rem;
  }

  .dice-display {
    width: 150px;
    height: 150px;
  }

  .dice-number {
    font-size: 2.5rem;
  }

  .payout-text {
    font-size: 1.1rem;
  }

  .roll-button {
    font-size: 1.1rem;
    padding: 0.6rem 1.2rem;
  }

  .info-button {
    font-size: 0.9rem;
    padding: 0.4rem 0.8rem;
  }

  .modal-content {
    padding: 1.5rem;
    max-width: 90%;
  }

  .bet-type-buttons {
    flex-wrap: wrap;
  }

  .bet-type-buttons button {
    flex: 1 0 40%;
    margin-bottom: 0.5rem;
  }
}

@media (max-width: 480px) {
  .dice-roller-container {
    padding: 1rem;
  }

  .dice-display {
    width: 120px;
    height: 120px;
  }

  .dice-number {
    font-size: 2rem;
  }

  .payout-text {
    font-size: 1rem;
  }

  .roll-container,
  .payout-container {
    padding: 1rem;
  }

  .roll-button {
    font-size: 1rem;
    padding: 0.5rem 1rem;
  }

  .info-button {
    font-size: 0.8rem;
    padding: 0.3rem 0.6rem;
  }

  .modal-content {
    padding: 1rem;
    max-width: 95%;
  }

  .bet-type-buttons button {
    flex: 1 0 100%;
  }

  .bet-buttons {
    flex-direction: column;
  }

  .bet-buttons button {
    margin-bottom: 0.5rem;
  }
}