.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 70px;
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--primary-bg);
  border-bottom: 2px solid var(--accent-color);
  z-index: 1000;
  color: var(--text-color);
  transition: all 0.3s ease;
}

.navbar.hidden {
  transform: translateY(-100%);
}

.logo {
  display: flex;
  align-items: center;
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--accent-color);
}

.logo img {
  width: 40px;
  height: 40px;
  margin-right: 0.5rem;
}

.navbar-content {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-grow: 1;
}

.nav-links {
  display: flex;
  gap: 1rem;
  margin-right: 1rem;
}

.nav-links button {
  background-color: transparent;
  border: 1px solid var(--accent-color);
  color: var(--accent-color);
  padding: 0.5rem 1rem;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.nav-links button:hover {
  background-color: var(--accent-color);
  color: var(--primary-bg);
}

.wallet-container {
  display: flex;
  align-items: center;
}

.menu-toggle {
  display: none;
  background: none;
  border: none;
  color: var(--accent-color);
  font-size: 1.5rem;
  cursor: pointer;
}

@media (max-width: 768px) {
  .navbar {
    height: auto;
    flex-wrap: wrap;
    padding: 0.5rem;
  }

  .menu-toggle {
    display: block;
  }

  .navbar-content {
    flex-basis: 100%;
    display: none;
    flex-direction: column;
    align-items: stretch;
    padding: 1rem 0;
  }

  .navbar-content.open {
    display: flex;
  }

  .nav-links {
    flex-direction: column;
    gap: 0.5rem;
    margin-right: 0;
    margin-bottom: 1rem;
  }

  .nav-links button {
    width: 100%;
  }

  .wallet-container {
    width: 100%;
  }
}

@media (max-width: 480px) {
  .logo {
    font-size: 1.2rem;
  }

  .logo img {
    width: 30px;
    height: 30px;
  }

  .nav-links button,
  .wallet-container button {
    font-size: 0.9rem;
    padding: 0.4rem 0.8rem;
  }
}